@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.gpt3__blog-container_article {
	width: 100%;
	height: 100%;

	display: flex;
	flex-direction: column;

	background: var(--color-footer);
}

.gpt3__blog-container_article-image {
	width: 100%;
	height: 100%;
	background: var(--color-bg);
}

.gpt3__blog-container_article-image img {
	width: 100%;
	height: 100%;
}

.gpt3__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;

    color: #fff;
    font-family: var(--font-family);
}


.gpt3__blog-container_article-content p,
.gpt3__blog-container_article-content a {
    font-size: 11.65px;
    font-weight: 700;
    line-height: 35px;
}

.gpt3__blog-container_article-content h3 {
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.30px;
    
    margin-bottom: 5rem;
    cursor: pointer;
}

.gpt3__blog-container_article-content a {
    cursor: pointer;
}

.gpt3__blog-container_article-content a:hover {
	-webkit-text-decoration: overline #ae67fa;
	        text-decoration: overline #ae67fa;
}

/* === Media queries === */
@media screen and (max-width: 550px) {
	.gp.gpt3__blog-container_article-content h3 {
		font-size: 18px;
		line-height: 25px;
	}
}



.gpt3__brand {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.gpt3__brand div {
	flex: 1 1;
	max-width: 150px;
	min-width: 120px;
	margin: 1rem;

	display: flex;
	justify-content: center;
	align-items: center;
}
.gpt3__cta {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-direction: row;

	padding: 2rem;
	margin: 4rem;
	border-radius: 1rem;
	background: var(--gradient-bar);
}

.gpt3__cta-content {
	display: flex;
	flex-direction: column;

	text-align: left;
	color: #fff;
}

.gpt3__cta-content p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
}

.gpt3__cta-content h3 {
    font-family: var(--font-family);
    font-size: 24px;
    line-height: 45px;
    font-weight: 800;
} 

.gpt3__cta-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: 2rem;
}

.gpt3__cta-btn button {
    background: #000;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border-radius: 2rem;

    min-width: 150px;
}

/* === Media queries === */
@media screen and (max-width: 650px) {
	.gpt3__cta {
		flex-direction: column;
	}

	.gpt3__cta-btn {
		margin: 2rem 0 0;
	}
}

@media screen and (max-width: 550px) {
	.gpt3__cta {
		margin: 4rem 2rem;
	}

	.gpt3__cta-content h3 {
		font-size: 18px;
		line-height: 32px;
	}

	.gpt3__cta-btn button {
		font-size: 14px;
		line-height: 28px;
	}	
}
.gpt3__features-container__feature {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	flex-direction: row;

	margin: 1rem;
}

.gpt3__features-container__feature-title {
	flex: 1 1;
	max-width: 180px;
	margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.gpt3__features-container_feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.gpt3__features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

/* === Media queries === */
@media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .gpt3__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .gpt3__features-container__feature {
        margin: 1rem 0;
    }
}
.gpt3__navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 2rem 6rem;
}

.gpt3__navbar-links {
	flex: 1 1;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

.gpt3__navbar-links_logo {
	margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
	width: 62.56px;
	height: 16.02px;
}

.gpt3__navbar-links_container {
	display: flex;
	flex-direction: row;
}

.gpt3__navbar-sign {
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
	color: #fff;
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 18px;
	line-height: 25px;
	text-transform: capitalize;

	margin: 0 1rem;
	cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
	padding: 0.5rem 1rem;
	color: #fff;
	background: #FF4820;
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 18px;
	line-height: 25px;
	border-radius: 5px;
	border: 0;
}

.gpt3__navbar-menu {
	margin-left: 1rem;

	display: none;
	position: relative;
}

.gpt3__navbar-menu svg {
	cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.gpt3__navbar-menu_container p {
	margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
	display: none;
}

/* === Media queries === */
@media screen and (max-width: 1050px) {
	.gpt3__navbar-links_container {
		display: none;
	}	

	.gpt3__navbar-menu {
		display: flex;
	}
}

@media screen and (max-width: 700px) {
	.gpt3__navbar {
		padding: 2rem 4rem;
	}
}

@media screen and (max-width: 550px) {
	.gpt3__navbar {
		padding: 2rem;
	}

	.gpt3__navbar-sign {
		display: none;
	}

	.gpt3__navbar-menu_container {
		top: 20px;
	}

	.gpt3__navbar-menu_container-links-sign {
		display: block;
	}
}
.gpt3__blog {
	display: flex;
	flex-direction: column;
}

.gpt3__blog-heading {
	width: 100%;
	text-align: left;

	margin-bottom: 5rem;
}

.gpt3__blog-heading h1 {
    font-size: 62px;
    line-height: 80px;
    font-family: var(--font-family);
    font-weight: 800;
}

.gpt3__blog-container {
	display: flex;
	flex-direction: row;
}

.gpt3__blog-container_groupA {
	flex: 0.75 1;
	margin-right: 2rem;
}

.gpt3__blog-container_groupB {
	flex: 1 1;

	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 2rem;
}

/* === Media queries */
@media screen and (max-width: 990px) {
	.gpt3__blog-container {
		flex-direction: column;
	}

	.gpt3__blog-container_groupA {
        margin: 2rem 0;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article-image {
		display: flex;
		flex-direction: column;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article {
       width: 100%;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article-image {
        height: 250px;
    }

    .gpt3__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
