	flex: 1 1;
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-direction: column;

	margin-right: 5rem;
}

.gpt3__header-content h1 {
	font-family: var(--font-family);
	font-weight: 800;
	font-size: 62px;
	line-height: 75px;
	letter-spacing: -0.04em;
}

.gpt3__header-content p {
	font-family: var(--font-family);
