    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}

.gpt3__header-image {
	flex: 1 1;
	display: flex;
	justify-content: center;
	align-items: center;
}

.gpt3__header-image img {
	width: 100%;
	height: 100%;
}

