.gpt3__whatgpt3-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

/* Customizing Feature component */
.gpt3__whatgpt3-container .gpt3__features-container__feature {
    margin: 0;
    flex: 1 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing Feature component */
.gpt3__whatgpt3-container ..gpt3__features-container__feature {
    max-width: 700px;
    margin-top: 1rem;
}

/* === Media queries === */
@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component*/
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}
* {
	box-sizing: border-box;
	padding: 0%;
	margin: 0%;
	scroll-behavior: smooth;
}

body {
	background: var(--color-bg);
}

a {
	color: unset;
	text-decoration: none;
}

.btn {
	border: none;
  outline: none;
  cursor: pointer;
}

.btn:hover {
	opacity: 0.7;
	transition: all 0.1.5s linear;
}

.gradient__bg {

	/* safari 5.1+,chrome 10+ */

	/* opera 11.10+ */

	/* ie 10+ */

	/* global 92%+ browsers support */
	background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
}

.gradient__text {
	background: var(--gradient-text);
	-webkit-background-clip: text;
	        background-clip: text;
	-webkit-background-clop: text;
	-webkit-text-fill-color: transparent;
}

.section__padding {
	padding: 4rem 6rem;
}

.section__margin {
	margin: 4rem 6rem;
}

/* Animations classes */
.scale-up-center {
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

/* Animations */
@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

/* === Media queries === */
@media screen and (max-width: 700px) {
	.section__padding {
		padding: 4rem;
	}

	.section__margin {
		margin: 4rem;
	}
}

@media screen and (max-width: 550px) {
	.section__padding {
		padding: 4rem 2rem;
	}

	.section__margin {
		margin: 4rem 2rem;
	}
}
